@import url("https://fonts.googleapis.com/css2?family=Domine:wght@600&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Parisienne&display=swap');
/* #region Default styles */

  body, html {
    height: 100%;
    color: black;
  }
  
.parallax{
  background-image: url("./assets/kjas.png");
  height: 100%;  
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;    
  padding-bottom: 100%;

  }

.titleContainer{
  font-family:  'Domine', 'Helvetica';
  position: -webkit-sticky;
  position: sticky;
  --maskX: 0;
  --maskY: 50; }
  .titleContainer h1 {
    font-size: 8vw;
    padding-bottom: 0px;width: 50.78px;
    margin: 0;
    line-height: 1.1em; }
.titleWrapper {
  line-height: 100vw;
  padding-bottom: 0px;width: 50.78px;
  color: #eeeeee;
  cursor: pointer; }

.cloneWrapper {
  line-height: 500 vw; 
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vw;
  color: #00adb5;
  -webkit-transition: all 0.8s cubic-bezier(0.165, 0.84, 0.44, 1);
  -o-transition: all 0.8s cubic-bezier(0.165, 0.84, 0.44, 1);
  transition: all 0.8s cubic-bezier(0.165, 0.84, 0.44, 1);
  -webkit-clip-path: polygon(0 0, calc(var(--maskX) * 1% + (var(--maskY) - 50) * .4%) 0, calc(var(--maskX) * 1% + (var(--maskY) - 50) * -.4%) 100%, 0 100%);
          clip-path: polygon(0 0, calc(var(--maskX) * 1% + (var(--maskY) - 50) * .4%) 0, calc(var(--maskX) * 1% + (var(--maskY) - 50) * -.4%) 100%, 0 100%); }
.page{
  background-color: #393e46;

}
p{
  
  color:#eeeeee;
  font-family: 'Domine', 'Helvetica';
  font-size: large;
  margin:2% 5% 2% 5% ;
  font-size: 4vw;
}
span{
  color:#00adb5;
  font-family: 'serif','Domine', 'Helvetica';
  font-size: 7vw;
  font-weight: bolder;
  

}



