html, body {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    background-color: #222831;
  }

  .video {
    position: absolute;
  left: 50%; /* relative to nearest positioned ancestor or body element */
  top: 50%; /*  relative to nearest positioned ancestor or body element */
  -webkit-transform: translate(-50%, -32%);
      -ms-transform: translate(-50%, -32%);
          transform: translate(-50%, -32%); /* relative to element's height & width */
    width: 119%;
    height: 150vw;
    display:block; }
    
  .pdf {
    display: -ms-flexbox;
    display: flex;
    position: absolute;
    color: rgb(36, 36, 36); 
    font-size: 120%; 
     line-height: 80%; padding: 2%; border-radius: 2%px; 
     font-family: 'Domine', 'Helvetica'; font-weight: 700; 
     text-decoration: none; font-style: normal; 
     font-variant: small-caps; text-transform: capitalize; 
     background:  rgb(87,192,255); 
     margin: 5% 40% 0 40%;
     border: 2px solid rgb(0, 0, 0); display: inline-block;}
    .pdf:hover {
    background: #f1f1f5; }
    .pdf:active {
    background: #bbc8d1; }

figure {
  margin: 0;
  position: relative;
}
figure h1 {
  position: absolute;
  top: 2%;
  left: 2%;
  border-right: 3px solid #fff;
  padding: 5px 10px 5px 5px;
  font-size: 150%;
}
figcaption{
  color: #fff;
  position:relative;
  top: 1vw;
 
}


.stretch {
  height: 2000%;
  background:rgb(59, 59, 68) ;
  min-height: 100%;
  min-width: 100%;
  background-size: cover;
  margin-top: 60vw;

}
.stretch h1 {
  padding: 19% 2% 0 0;
  color: rgb(255, 255, 255);
    padding-bottom: 100%;
  font-size: 150%;

}
/*PALETA DE COLORES
rgb(99, 115, 255) azumoradon
rgb(255,200,125)  beigesito
rgb(255,252,74)   amallillo
rgb(175,112,255)  morado
rgb(87,192,255)azul clalrito*/
.hero-wrapper {
  position: fixed;
  bottom: 0;
  right: 0;
  height: auto;
  width: auto;
  min-height: 100%;
  min-width: 100%;
  z-index: -9999; 
}



